@import url(https://fonts.googleapis.com/css?family=Oxygen&display=swap);
:root{
  --main-bg-color: rgb(221, 221, 221);
}

body{
  margin:0px;
  font-family: 'Oxygen', raleway ;
  font-weight: 100;
}

#root{
  top:0px;
}

.headerFrame{
  width: 100%;
  text-align: center;
  height: 7vh;
  background:rgb(221, 221, 221);
  background:var(--main-bg-color);
  margin:0px;
}

.headerFrame h1{
  margin:0px
}

.windowFrame{
  height: 100%;
  width:100%;
  display:table;
  background:rgb(221, 221, 221);
  background:var(--main-bg-color);
}

.projectListFrame{
  display: table-cell;
  width:15%;
  height:90vh;
  background:rgb(221, 221, 221);
  background:var(--main-bg-color);
  padding-top:1vh
}

.projectDetailFrame{
  display: table-cell;
  width:84.5%;
  border-top-left-radius: 10px;
  height:95vh;
  background:white;
  border:1px black solid;
}



hr{
  width: 80%;
  margin-left:0;
  border:0;
  border-top: 1px solid rgb(0, 225, 255);
}





.project-description{
    margin-left:5%;
    margin-top:20px
  }


.spacer-col{
  width:40px;
}

#task-list-container{
  margin-left:30px;
}

.task-check-container{
  height:20px;
  width:20px;
  border-radius:5px;
  border:1px solid black;
  display: inline-block;
  -webkit-transition: border .2s ease;
  transition: border .2s ease;
}

.task-container{
  width:80%;
  height:auto;
  display: block;
  -webkit-transition: .2s ease;
  transition: .2s ease;
  margin-bottom:10px;
  -webkit-transition: color .2s ease;
  transition: color .2s ease;
  border: 1px transparent;
  margin-bottom:10px;
}

.complete-task{
  font-size: 15px;
  text-align: center;
  color:green
}

.complete-task:hover{
  color:red;
}

.task-container:hover{
  background: rgba(0, 0, 0, 0.575);
  color: rgb(0, 225, 255);
  border: 1px solid rgb(0, 225, 255);
  border-radius:5px;
}

.task-container:hover .task-check-container{
  border-radius: 5px 0px 5px 0px;
  color: rgb(0, 225, 255);
  border: 1px solid rgb(0, 225, 255);
  border-left:none;
  border-top:none;
}

.task-container:hover .task-remove-container{
  display:inline-block;
  height:20px;
  width:20px;
  border: 1px solid rgb(0, 225, 255);
  border-top:none;
  border-right:none;
  border-radius:0px 0px 0px 5px;
}

.task-quick{
  display:block;
  position: relative;
}

.task-remove-container{
  display:none;
  text-align: center;
  position: absolute;
  right:0;
  top:0;
}

.task-remove-container:hover{
  color:red;
}

.task-title-container{
  display: inline-block;
  margin-left:10px;
}

.task-detail{
  position: relative;
  display:block;
  margin-left: 30px;
  -webkit-animation: scaleInAnimation ease .5s;
          animation: scaleInAnimation ease .5s;
  -webkit-transform-origin: top;
          transform-origin: top;
}


@-webkit-keyframes scaleInAnimation { 
  0% { 
      -webkit-transform: scaleY(0); 
              transform: scaleY(0); 
  } 
  100% { 
      -webkit-transform: scaleY(1); 
              transform: scaleY(1); 
   } 
  }


@keyframes scaleInAnimation { 
  0% { 
      -webkit-transform: scaleY(0); 
              transform: scaleY(0); 
  } 
  100% { 
      -webkit-transform: scaleY(1); 
              transform: scaleY(1); 
   } 
  }


  .add-task{
    display: inline-block;
    width:40px;
    height:20px;
    
    -webkit-transition: .25s ease;
    
    transition: .25s ease;
    text-align:center;
    border-radius:50%;
    margin:0px;
    margin-bottom:10px;
  }

  .plusIcon{
    color:black;
    -webkit-transition:.2s ease;
    transition:.2s ease;
    font-size: 30px;
  }

  .circleIcon{
    color: rgb(0, 225, 255);
    -webkit-transition: .2s ease;
    transition: .2s ease;
    font-size: 25px;
  }

  .add-task:hover .circleIcon{
    color:black;
  }

  .fa-layers{
    text-align: center;
    font-size:24px
  }

  .add-task:hover .plusIcon{
    color: rgb(0, 225, 255)
  }



  #page-mask {
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  .project-title{
    margin-left:50px;
  }

  #add-project-form {
    height: 400px;
    width: 600px;
    position:fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 10%;
    background:black;
    border-radius:5px;
    color:white;
  }

form input[type=text], form textarea{
  width: 85%;
  
  margin:auto;
  margin:10px;
  resize: none;
  border:1px transparent;
  border-radius:3px;
  padding:10px;
}

form input[type=text]{
  height:20px;
}

form ul{
  list-style: none;
  padding-left:40px;
  padding-right:40px;
}


form input[type=submit]{
  width: 60px;
  height: 30px;
  border: 1px transparent;
  border-radius:3px;
  margin:5px;
  background: rgb(0, 225, 255);
  color: black;
  border: 1px solid black;
  -webkit-transition: .25s ease;
  transition: .25s ease;
}

form input[type=submit]:hover{
  background: black;
  color: rgb(0, 225, 255);
  border: 1px solid rgb(0, 225, 255);
}

#submit-btn-container{
  display: flex;
  justify-content: center;
}

.project-complete-container{
  width:40px;
  height:40px;
  position: absolute;
  top:0;
  left:0;
  border-radius:10px 0px 5px 0px;
  border:1px solid black;
  border-left: 1px transparent;
  border-top:1px transparent;
  -webkit-transition:.3s ease;
  transition:.3s ease;
}

.projectDetailFrame{
  position: relative;
}

.project-complete-container:hover{
  background:rgba(0, 128, 0, 0.411)
}

.complete-project{
  font-size: 30px;
  text-align: center;
  color:green;
}

.complete-project:hover{
  color:red;
  background: transparent
}

.project-item{
    display: block;
    width:97%;
    -webkit-transition: .25s ease;
    transition: .25s ease;
    background-color: rgb(192, 192, 192);
    text-align: center;
    border-radius:10px 0px 0px 10px;
    float:right;
    margin-bottom:5px;
  }
  
  .project-item:hover{
    background:rgb(129, 129, 129);
  }


  .remove-project-button{
    width: 30px;
    position:relative;
    top: 0px;
    left:0px;
    z-index: 900;
    border-radius:5px 0px 5px 0px;
    border: 1px rgb(0, 225, 255) solid;
    border-left:1px transparent;
    border-top:1px transparent;
    background:rgb(31, 31, 31);
    display: none;
  }

  .project-item-text{
    padding:20px;
  }

  .active-project .project-item-text{
    padding-top:0px;
  }
  
  .active-project .remove-project-button{
    display: block
  }

  .active-project .remove-project-button:hover{
    color:red
  }

  .active-project{
    background: black;
    color: rgb(0, 225, 255);
    border: 1px solid rgb(0, 225, 255);
    width:90%;
    border-right:none;
  
  }
  
  .active-project:hover{
    background:black;
    
  }
  
  .add-project{
    display: block;
    width: 50%;
    padding-top:10px;
    padding-bottom:10px;
    -webkit-transition: .25s ease;
    transition: .25s ease;
    background:rgb(0, 225, 255);
    color: black;
    border:1px solid black;
    text-align:center;
    border-radius:10px;
    margin:auto;
    margin-bottom:10px;
  }
  
  .add-project:hover{
    background: black;
    color: rgb(0, 225, 255);
    border: 1px solid rgb(0, 225, 255);
  }

  #page-mask {
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 900;
  }

  #add-project-form {
    height: 400px;
    width: 600px;
    position:fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 10%;
    background:black;
    border-radius:5px;
    color:white;
    z-index: 900;
  }

